<template>
    <div class="staking-deposits">
        <button class="back-button" @click="goBack">
          <img class="back-button-arrow" src="@/assets/newImages/black_arrow.png"/>
          <p class="back-button-text">{{$localizeFilter('staking')}}</p>
        </button>
        <p class="title">{{$localizeFilter('deposits_to_staking')}}</p>
        <StakingDepositsTable
        :showTable="deposits.length > 0"
        :deposits="deposits" @changeStatus="changeStatus" @cancelRequest="cancelRequest"/>
        <EmptyTable v-if="deposits.length == 0" :text="$localizeFilter('no_deposits_to_staking')"/>
        <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
    </div>
  </template>
  
  <script>
  import { defineComponent } from '@vue/composition-api'
  import StakingDepositsTable from '@/components/StakingDepositsTable'
  import paginationMixin from '@/mixins/pagination.mixin'
  import EmptyTable from "@/components/EmptyTable"
  
  export default defineComponent({
    props: {
    },
    components: {
      StakingDepositsTable, EmptyTable
    },
    mixins: [paginationMixin],
    watch:{
        page() {
            this.$nextTick(this.loadData);
        },
    },
    setup() {
      
    },
    data: () => ({
      deposits: [],
      count: 0,
      status: 'all'
    }),
    mounted() {
        this.loadData()
    },
    methods: {
       goBack() {
          this.$router.back()
       },
       cancelRequest(idx) {
         this.requests[idx].status = 'canceled'
         this.$forceUpdate()
       },
       changeStatus(status) {
         this.status = status
         this.loadData()
       },
       async loadData() {
           try {
            const data = {status: this.status, skip: this.skip, limit: this.limit, currencyCode: 'CWPT', type: this.from}
              this.deposits = await this.$store.dispatch('getStakingDeposits', data)
              const {count} = await this.$store.dispatch('getStakingDepositsCount', data)
              this.count = count
              this.setupPagination(this.deposits)
          } catch(e) {
              this.$error(e)
          } 
       }
    }
  })
  </script>
  
